/*
 * Основная сетка сайта
 *
 */

// Объявляем блок
$block: '.l-page';

// Стили
#{$block} {
    width: 100%;
    min-height: 100%;
    margin: 80px auto 0;
    padding: 0 20px;

    &__wrapper {
        padding: 0; }

    // Tablet
    @include bp-tablet {

        & {
            margin: 0 auto;
            width: 710px;
            padding: 0; } }

    // Tablet landscape
    @include bp-tabletLandscape {

        & {
            width: 910px; } }

    // Desktop
    @include bp-desktop {

        & {
            width: 1180px; } }

    // Desktop full
    @include bp-desktopFull {

        & {
            width: 1250px; } } }
