/*
 * Блок вопросов
 *
 */


// Объявляем блок
$block: '.b-questions';

// Стили
#{$block} {

    &__name {
        font-size: 16px;
        font-family: "NotoSerif", sans-serif;
        font-style: italic;
        font-weight: bold;
        margin-bottom: 15px; }

    &_latest,
    &_similar {
        border: 1px solid $grey-cloud;
        padding: 15px 20px 0;
        margin-bottom: 20px; }

    @include bp-tabletLandscape {

        & {
            &_latest,
            &_similar {
                margin-bottom: 30px; } } }

