/*
 * Лента новостей рубрик
 *
 */

// Объявляем блок
$block: '.b-news-rubric';

// Основной блок
#{$block} {
    @include font(13px);

    margin: 0 20px 20px;

    &__title {
        @include font(15px);
        font-style: italic;

        margin-bottom: $indent-title;
        //padding-bottom: 15px

        position: relative;

        border-bottom: 1px solid transparent; }

    &__title-icon {
        width: 20px;
        height: 10px;

        position: absolute;
        right: 0;
        top: 8px;

        background-position: 0px -20px; }

    &__content {
        max-height: 1000px;
        //padding-bottom: 20px
        padding-bottom: 0;
        overflow: hidden;
        background-color: $today-bg-color; }

    &__item {
        width: 100%;
        margin-bottom: 5px;

        display: inline-block;
        vertical-align: top;
        line-height: 1.6em; }

    &__item-image {
        width: 100%; }

    &__item-image-wrapper {
        width: 100%;
        //margin-bottom: 15px

        display: inline-block;
        position: relative;
        line-height: 0; }

    &__item-time {
        @include font(13px);

        padding: 4px;

        position: absolute;
        bottom: 0;
        left: 0;

        display: inline-block;
        line-height: 1em;
        color: $white;
        background-color: $time-color; }

    &__item-description {
        margin-top: 15px;
        padding: 0 20px; }

    &__item-title {
        @include font;

        margin-bottom: 10px;

        line-height: 1.3em; }

    &__item-text {
        @include font(13px); }

    &__item-tags {
        margin-top: 10px;
        color: $comment-color; }

    &__list {
        padding: 0 20px; }

    &__list-item {
        margin-top: 10px;
        padding-top: 10px;

        overflow: hidden;
        border-top: 1px solid $border-color; }

    &__list-item-image {
        max-width: 100px;
        margin: 5px 0 10px 10px;

        float: right;
        display: none; }

    &__list-item-time {
        margin-bottom: 2px;
        color: $comment-color; }

    &__list-item-link {
        @include font(14px);
        font-family: $f-serif;

        line-height: 1.6em;

        &:hover {
            color: $link-color; } }

    &__advert {
        display: none; }

    // Модификатор если лента новостей
    &_feed &__title,
    &_feed-article &__title {
        margin-bottom: 10px; }

    &_feed &__content,
    &_feed-article &__content {
        max-height: none;
        padding-bottom: 0;
        background-color: transparent;
        border: 1px solid $today-bg-color; }

    &_feed &__list,
    &_feed-article &__list {
        width: 100%;
        margin-left: 0;
        padding: 20px; }

    &_feed &__list-item:first-child,
    &_feed-article &__list-item:first-child {
        margin-top: 0; }

    &_feed &__list-item-link,
    &_feed-article &__list-item-link {
        font-family: $f-sans; }

    // Tablet
    @include bp-tablet {

        & {
            width: 100%;
            margin: 0 auto 20px;
            //padding-top: 15px

            &__item,
            &__list {
                width: 235px;
                display: inline-block;
                vertical-align: top; }

            &__list {
                margin-left: 5px;
                padding: 5px 0 0 10px; }

            &__list-item {

                &:first-child {
                    padding-top: 0;
                    border-top: 0; } }

            // добавил Леха! на запуск
            &__content {
                padding-bottom: 20px; } } }


    // Tablet landscape
    @include bp-tabletLandscape {

        & {

            &__item,
            &__list {
                width: 310px; }

            &__list {
                margin-left: 10px; }

            &__list-item-image {
                display: block; } } }


    // Desktop
    @include bp-desktop {

        & {

            &_feed &__list,
            &_feed-article.b-sidebar-right__item &__list {
                width: 310px; }

            &__item-title {
                @include font(17px); } } }


    // Desktop full
    @include bp-desktopFull {

        & {

            &__item,
            &__list {
 }                //width: 310px

            &__list {
                margin-left: 20px; }

            &__advert {
                width: 310px;
                margin-left: 30px;
                padding: 60px 20px;

                display: inline-block;
                vertical-align: top;
                text-align: center; } } } }
