/*
 * Блок Ссылок в виде кнопки
 *
 */

// Объявляем блок
$block: '.b-link-btn';

// Стили
#{$block} {
    display: inline-block;

    @include button-field($link-color);

    // Если второстепенное поле
    &_second {
        @include button-field($time-color); }

    // Если поле с акцентом
    &_accent {
        @include button-field($red); }

    &_thanx {
        display: block;
        margin-bottom: 20px;

        @include button-field($time-color); }

    &_thanx-disabled {
        @include button-field($green-yellow);

        &:hover {
            background-color: $green-yellow; } }

    @include bp-tabletLandscape {

        & {
            &_thanx {
                display: inline-block;
                margin-bottom: 0; } } } }
