// Объявляем блок
$block: '.b-sidebar-right';

// Стили
#{$block} {
    display: none;
    margin: 0;

    // Desktop
    @include bp-desktop {

        & {
            width: $width-block-small;

            display: inline-block;
            vertical-align: top;

            &__item {
                margin-bottom: $indent-v-el; } } }

    // Desktop full
    @include bp-desktopFull {

        & {
            float: right; } } }
