/*
    * Блок вопроса
    *
    */

// Объявляем блок
$block: '.b-question-block';

// Стили
#{$block} {
    border: 1px solid $border-color;
    padding: 20px;
    margin-bottom: 20px;
    font-size: 13px;
    word-wrap: break-word;

    &_card {
        position: relative;
        margin-bottom: 24px;
        &:after {
            content: "";
            position: absolute;
            bottom: -5px;
            height: 4px;
            border-width: 0 1px 1px;
            border-style: solid;
            border-color: $border-color;
            right: 9px;
            left: 9px; } }

    &__img-col {
        display: none;

        &_visible {
            display: inline-block; } }

    &__top {
        margin-bottom: 10px; }

    &__author,
    &__date {
        @include font(13px);
        color: $comment-color; }

    &__title {
        font-family: "NotoSerif", serif;
        font-weight: bold;
        margin-bottom: 10px;
        font-size: 16px; }

    &__text {
        line-height: 22px;
        margin-bottom: 10px;
        & > p {
            margin-bottom: 7px;

            & > a {
                color: $link-color; } }

        & > ul {
            margin: 0 0 7px 0;

            & > li {
                position: relative;
                padding-left: 25px;
                margin-bottom: 7px;

                &:before {
                    content: '';
                    position: absolute;
                    top: 9px;
                    left: 0;
                    width: 5px;
                    height: 5px;
                    border-radius: 50%;
                    background-color: $red; } } } }

    &__tags {
        @include font(13px);
        color: $comment-color;
        display: inline-block; }

    &__bottom {
        @include font(13px);
        border-top: 1px solid $border-color;
        margin-top: 15px;
        padding-top: 18px;

        &_highlighted {
            border-color: $highlighted-question-border; } }

    &__bottom-title {
        margin-bottom: 5px; }

    &__reply-link {
        display: inline-block; }

    &__media {
        border-radius: 50%;
        vertical-align: middle;
        margin-right: 1px; }

    &_highlighted {
        background: $highlighted-question-bg;
        border-color: $highlighted-question-border;
        padding-top: 20px;

        & > .b-img-col .b-round-image {
            display: none; }

        & .b-question-block__bottom .b-round-image {
            display: inline-block; }

        & .b-question-block__body {
            display: inline-block;
            vertical-align: top; } }

    &_latest,
    &_similar {
        border-width: 0 0 1px 0;
        margin-bottom: 10px;
        padding: 0;
        &:last-child {
            border: none;
            margin-bottom: 5px; }

        & .b-question-block__title {
            font-size: 13px;
            font-weight: normal; } }

    &_bibliotechka {
        display: none; }


    @include bp-tablet {

        & {
            padding: 15px 20px;

            &__img-col {
                &_visible {
                    display: none; } }

            &_card {
                &:after {
                    width: 430px;
                    left: 9px; } }

            &__body {
                width: 100%;
                margin: 0; }

            &__date {
                margin-left: 15px; }

            &__bottom-title {
                display: inline; }

            &__reply-link {
                margin-left: 6px; }

            &_latest,
            &_similar {
                padding: 0; } } }

    @include bp-tabletLandscape {

        & {
            margin-bottom: 30px;
            font-size: 14px;

            &_latest,
            &_similar {
                font-size: 13px;
                margin-bottom: 10px; }

            &__img-col {
                &_visible {
                    display: inline-block; } }

            &__title {
                font-size: 17px; }

            &_card {
                margin-bottom: 30px;
                &:after {
                    width: 626px;
                    left: 7px; } }

            &_highlighted {
                & > .b-img-col .b-round-image {
                    display: inline-block; }

                & .b-question-block__bottom .b-round-image {
                    display: none; }

                & .b-question-block__body {
                    margin-left: 30px;
                    width: calc(100% - 131px); } } } }

    @include bp-desktop {

        & {
            &_card {
                &:after {
                    width: 626px; } } } }

    @include bp-desktopFull {

        & {
            padding: 25px 30px 15px;

            &__bottom {
                margin-bottom: 10px; }

            &_card {
                &:after {
                    width: 696px; } }

            &_latest,
            &_similar {
                padding: 0; } } } }
