/*
 * Низ шапки
 *
 */
// Объявляем блок
$block: '.b-menu';

// Переменные

// Стили
#{$block} {

    display: flex;
    justify-content: space-between;
    height: 1px; // фикс для старых айфонов, без него под заголовком "Юридическая консультация" в шапке появляется лишний отступ

    &__section {
        display: inline-block;
        position: relative;
        &_right {
            position: absolute;
            top: 15px;
            right: 20px;
            padding-top: 5px; } }

    &__mobile {
        background: $white;
        position: absolute;
        width: 100%;
        display: none; }

    // Tablet
    @include bp-tablet {

        & {
            padding-top: 25px;
            height: auto; // добавлено, чтобы нейтрализовать свойство, объявленное для мобильников (height: 1px)

            &__section {

                &_right {
                    position: static;
                    margin-top: -10px; } }

            &_fixed {
                position: fixed;
                top: 0;
                width: 730px;
                margin-left: -10px;
                padding: 20px 10px 0;
                box-shadow: 0 0 60px -3px $box-shadow;
                background: $white;
                z-index: 9; } } }

    @include bp-tabletLandscape {

        & {

            &_fixed {
                width: 930px; } } }

    @include bp-desktop {

        & {

            &_fixed {
                width: 1200px; } } }

    @include bp-desktopFull {

        & {

            &_fixed {
                width: 1270px; } } } }
