/*
 * Шапка статьи
 *
 */

// Объявляем блок
$block: '.b-material-head';

// Стили
#{$block} {
    @include font(13px);
    color: $comment-color;
    margin-bottom: 12px;

    &__title {
        // добавил по просьбе Оли и Макса и Агимы (было 18px)
        @include font(21px);

        color: $black;
        line-height: 1.2em;

        margin-bottom: 10px; }

    &__rubric {
        display: block; }

    &__date {
        padding-right: 20px; }

    &__date-name {
        margin-left: 15px; }

    &__rubric-name {
        color: $black; }

    &__date,
    &__rubric {
        @include font(13px); }

    // Tablet
    @include bp-tablet {

        & {
            &__title {
                @include font(21px); }

            &__date,
            &__rubric {
                width: auto;
                float: right; } } }

    // Tablet landscape
    @include bp-tabletLandscape {

        & {
            &__title {
                @include font(26px); } } }

    // Desktop
    @include bp-desktop {

        & {
            //margin: 0

            &__title {
                @include font(32px); } } }

    // Desktop full
    @include bp-desktopFull {

        & {
            &__title {
                @include font(22px);
                font-weight: bold; } } } }
