/*
 * Блок шеринга статьи
 *
 */

// Объявляем блок
$block: '.b-share';

// Стили
#{$block} {
    @include font(14px);

    // ЧТоб на мобиле при нажатии не шарился фейсбук
    &:before {
        content: '';
        display: block;
        position: absolute;
        width: 20px;
        height: 20px;
        left: 0;
        top: 5px;
        z-index: 10; }

    &_fast-access {}


    &__item {
        display: inline-block;
        vertical-align: middle; }

    &__content {}

    // Кастомизируем yandex share
    & .b-share_ya-share {

        .ya-share2__item {
            margin: 0;

            .ya-share2__badge {
                overflow: inherit;
                background-color: transparent; }

            .ya-share2__counter {
                padding: 0;

                position: absolute;
                top: -8px;
                left: 25px;

                color: $comment-color;

                &:before {
                    display: none; } }

            .ya-share2__icon {
                width: 30px;
                height: 30px;
                display: block;
                overflow: hidden;

                background: url(#{$img-path}social_icons.svg) no-repeat;

                @include rounded-corners(100%);
                @include transition; } }

        .ya-share2__item_service_facebook .ya-share2__icon {
            background-position: 0 0;
            background-color: $facebook-color;

            &:hover {
                background-color: $facebook-color + #111; } }

        .ya-share2__item_service_gplus .ya-share2__icon {
            background-position: -30px 0;
            background-color: $google-color;

            &:hover {
                background-color: $google-color + #111; } }

        .ya-share2__item_service_twitter .ya-share2__icon {
            background-position: -60px 0;
            background-color: $twitter-color;

            &:hover {
                background-color: $twitter-color + #111; } }

        .ya-share2__item_service_vkontakte .ya-share2__icon {
            background-position: -90px 0;
            background-color: $vkontakte-color;

            &:hover {
                background-color: $vkontakte-color + #111; } }

        .ya-share2__item_service_odnoklassniki .ya-share2__icon {
            background-position: -120px 0;
            background-color: $odnoklassniki-color;

            &:hover {
                background-color: $odnoklassniki-color + #111; } } }

    // Модификаторы

    // Горизонтальный

    &_horizontal &__item {

        // в телефоне наползают на слово поделиться < 320
        margin-top: 10px;

        .b-text-actions & {
            margin-top: 0; } }

    .b-gallery &_horizontal &__item {
        margin-top: 0; }

    &_horizontal .b-share_ya-share {

        .ya-share2__item {
            margin-right: 10px;

            .b-text-actions & {
                margin-right: 5px;
                margin-left: 5px;

                &:last-child {
                    margin-right: 5px; } }

            &:last-child {
                margin-right: 0; } } }

    &_horizontal &__content {
        margin-right: 10px; }

    // Вертикальный
    &_vertical .b-share_ya-share {

        .ya-share2__item {
            margin-bottom: 10px;
            display: block;

            &:last-child {
                margin-right: 0; } } }

    @include bp-tablet {

        & {

            &_horizontal &__item {
                margin-top: 0; } } } }
