/*
 * Поле поиска
 *
 */

// Объявляем блок
$block: '.b-search-form';
$search-results: '.b-search-results__form';

// Модификаторы

// Стили
#{$block} {

    background: $white;
    position: absolute;
    top: 10px;
    right: 20px;
    left: 20px;
    width: calc(100% - 40px);
    padding-bottom: 1px;
    line-height: 22px;

    &__input {
        width: 150px;
        border: none; }

    &__select-wrapper {
        display: inline-block;
        position: relative;

        &:after {
            content: "";
            position: absolute;
            display: block;
            top: 15px;
            right: 5px;
            border: 3px solid;
            border-color: #000 transparent transparent transparent; } }

    &__select {
        border: none;
        width: 115px;
        padding: 5px 10px; }

    @include bp-tablet {

        & {
            top: 3px;

            &__input {
                width: 360px; } } }

    @include bp-tabletLandscape {

        & {
            &__input {
                width: 560px; } } }

    @include bp-desktop {

        & {
            &__input {
                width: 830px; } } }

    @include bp-desktopFull {

        & {
            &__input {
                width: 900px; } } } }

#{$search-results} {

    #{$block} {

        &__select-wrapper {
            margin-right: 20px;
            margin-bottom: $indent-v-el;
            width: 100%;

            &:after {
                top: 17px;
                right: 8px; } }

        &__select {
            border: 1px solid $border-color;
            padding: 8px 10px;
            width: 100%; }

        @include bp-tablet {

            & {

                &__select-wrapper {
                    width: auto;
                    margin-bottom: 0; }

                &__select {
                    width: 215px; } } }

        @include bp-tabletLandscape {

            & {
                &__select-wrapper {
                    margin: 0 20px; }

                &__select {
                    width: 125px; } } } } }
