// Объявляем блок
$block: '.b-search-overlay';

// Модификаторы
$bSearchBar: '.b-search-bar';

// Стили
#{$block} {
    background-color: $white;

    &__popular {
        width: 100%;
        padding: 6px 20px; }

    &__popular-title {
        margin-bottom: 10px;
        color: $comment-color; }

    &__popular-list {}

    &__popular-list-item {
        margin-bottom: 12px; }

    // Модификация search bar
    #{$bSearchBar} {
        //width: 100%
        margin-bottom: 10px;
        padding: 10px;

        //border-bottom: 1px solid $border-color

        // Контейнер
        & .b-field_input_text {
            @include font(15px);
            width: calc(100% - 100px);

            display: inline-block;

            position: relative;
            border: 0;

            &:before {
                content: 'Найти на сайте:';
                position: absolute;
                top: 6px;
                left: 0;
                color: $comment-color;

                display: none; } }

        // Элемент
        & .b-field__input_text {
            border: 0; }

        // Элемент
        & .b-field__input_submit {
            padding-left: 8px;
            padding-right: 8px; }

        &:after {
            content: '';
            width: 100%;
            position: absolute;
            top: 58px;
            left: 0;
            border-bottom: 1px solid $border-color; } }


    // Tablet
    @include bp-tablet {

        & {
            padding: 0 20px;

            &__popular-title {
 }                //margin-bottom: 20px

