        & {
            padding: 0;

            // Модификация search bar
            #{$bSearchBar} {
                width: 1240px;
                padding-left: 260px;

                &__field_search {
                    width: 840px; } }

            &__popular {
                //width: 990px
                width: 970px;
                //margin-left: 340px
                margin-left: 250px; } } }

