/*
 * Хлебные крошки
 *
 */

// Объявляем блок
$block: '.b-breadcrumbs';

// Стили
#{$block} {
    display: none;

    &__item {
        list-style: none;
        display: inline-block;
        &:after {
            content: "/";
            margin: 0 5px; }
        &:last-child {
            &:after {
                content: "";
                display: none; } } }

    @include bp-tablet {

        & {
            display: block;

            &__list {
                margin: -5px 0 15px; } } }

    @include bp-tabletLandscape {

        & {
            &__list {
                margin: -5px 0 25px; } } } }
