// Объявляем блок
$block: '.b-form';

// Стили
#{$block} {

    &__body {
        margin-bottom: 20px;
        padding: 15px 20px;
        position: relative;
        display: block;

        &_grey {
            background: $today-bg-color; }

        &_info {
            border: 1px solid $green-accent; } }

    &__title {
        margin-bottom: 15px;
