/*
 * Счетчики
 *
 */

// Объявляем блок
$block: '.b-counter';

// Стили
#{$block} {
    width: 100%;
    margin-top: 20px;
    text-align: center;

    &__item {
        margin-left: 8px;
        display: inline-block;
        &:first-child {
            margin-left: 0; } }


    // Tablet
    @include bp-tablet {

        & {
            margin-top: 0;
            text-align: right;

            &__item {
                margin-left: 10px;
                display: inline-block; } } }

    // Desktop
    @include bp-desktop {

        & {
            margin-top: 20px; } } }
