/*
 * Список юристов
 *
 */


// Объявляем блок
$block: '.b-jurists';

// Стили
#{$block} {

    &_feed {
        padding: 15px 20px;
        background: $today-bg-color;

        & > .b-jurists__name {
            @include font(16px);
            font-family: "NotoSerif", serif;
            font-style: italic;
            font-weight: bold;
            margin-bottom: $indent-v-el;
            color: $red-orange; } }

    &_top {

        & > .b-jurists__name {
            font-size: 16px;
            font-family: "NotoSerif", sans-serif;
            font-style: italic;
            font-weight: bold;
            margin-bottom: 15px; } }

    &_feed {
        margin-bottom: 20px; }

    &_list {

        &.has-loading {
            opacity: 0.5; } }

    @include bp-tabletLandscape {

        & {
            &_feed,
            &_top {
                margin-bottom: 20px; } } }

    @include bp-tablet {

        & {
            &_feed,
            &_top {
                margin-bottom: 30px; } } } }
