/*
 * Блок пошаговой инструкции
 *
 */


// Объявляем блок
$block: '.b-answer-steps';

// Стили
#{$block} {
    padding: 0;
    list-style-type: none;

    &__item {
        counter-increment: step-counter;
        background: $white;
        position: relative;
        padding: 15px 15px 15px 65px;
        margin-bottom: 20px;
        &:last-child {
            margin-bottom: -10px; }
        &:before {
            display: block;
            position: absolute;
            top: 20px;
            left: 20px;
            content: counter(step-counter);
            background-color: $red-orange;
            -webkit-border-radius: 50%;
            -moz-border-radius: 50%;
            border-radius: 50%;
            color: $white;
            font-size: 16px;
            font-weight: bold;
            width: 25px;
            height: 25px;
            text-align: center;
            line-height: 26px; } }

    &__title {
        font-size: 16px;
        font-weight: bold;
        margin-bottom: 5px; } }
